import { useState } from "react";
import style from "../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useAdmissionFeeRegisterExport } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import InstituteUniversalBatchDorpdown from "../../InstituteUniversalBatch/InstituteUniversalBatchDorpdown";
import InstituteUniversalDepartment from "../../InstituteUniversalDepartment/InstituteUniversalDepartment";
import FeeStatisticsBankFilter from "../../FinanceManager/FeeStatistics/FeeStatisticsBankFilter";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const AdmissionFeeRegister = ({ onClose, fid, instituteId }) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();
  const getQuery = useLocation();
  const [state, setState] = useState({
    from: "",
    to: "",
    batch: "",
    bank: "",
    department: [],
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [step, setStep] = useState("");
  const [admissionFeeRegisterExport] = useAdmissionFeeRegisterExport();

  const onTransactionDate = (val, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(val),
    }));
  };

  const onFilter = () => {
    let validation = requiredField(state, ["bank", "department"]);
    if (
      existingRequiredField(validation) &&
      fid &&
      state.department?.length > 0
    ) {
      setDisabled((pre) => !pre);
      admissionFeeRegisterExport({
        fid: fid,
        bank: state?.bank?._id ?? "",
        from: state.from,
        to: state.to,
        bodyParameter: {
          batch: state.batch?._id,
          depart: state.department,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          let url =
            getQuery.state?.urlOffSet === "Institute"
              ? `/q/${params.username}/institute/member/finance?type=export`
              : `/q/${params.username}/member/staff/finance?type=export`;
          navigate(url, {
            state: getQuery.state,
          });
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  const onSelectBatch = (val) => {
    setState((prev) => ({
      ...prev,
      batch: val,
    }));
  };
  const onCloseStep = () => {
    setStep("");
  };
  const onSelectBank = (value) => {
    setState((pre) => ({
      ...pre,
      bank: value,
    }));
  };
  const onSelectDepartment = (val) => {
    if (state.department?.includes(val?._id)) {
      setState((pre) => ({
        ...pre,
        department: pre.department?.filter((dt) => dt !== val?._id),
      }));
    } else {
      setState((pre) => ({
        ...pre,
        department: [...pre.department, val?._id],
      }));
    }
  };
  return (
    <>
      {step === 1 ? (
        <InstituteUniversalDepartment
          title={t("select_department")}
          onClose={onCloseStep}
          selectList={state.department}
          batchId={state.batch?._id}
          isMultiSelect
          onMultiSelect={onSelectDepartment}
          onDone={onCloseStep}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("admission_fee_register_export")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormCalendarInput
                label={t("from")}
                placeholder={t("please_select_from_date")}
                name={"from"}
                value={state.from ? slashDateFormat(state.from) : ""}
                onChange={(e) => onTransactionDate(e, "from")}
                errorShow={errorField["from"]}
              />
              <FormCalendarInput
                label={t("to_only")}
                placeholder={t("please_select_to_date")}
                name={"to"}
                value={state.to ? slashDateFormat(state.to) : ""}
                onChange={(e) => onTransactionDate(e, "to")}
                errorShow={errorField["to"]}
              />
              <FeeStatisticsBankFilter
                fid={fid}
                onSelect={onSelectBank}
                viewAs="NORMAL_FILTER"
                defalutValue={state?.bank?.finance_bank_account_name ?? ""}
                customListStyle={{
                  position: "absolute",
                  width: "100%",
                  zIndex: "13",
                  top: "5.4rem",
                }}
              />
              <InstituteUniversalBatchDorpdown
                onSelect={onSelectBatch}
                instituteId={instituteId}
                errorShow={errorField["batch"]}
                defalutValue={state?.batch?.batchName}
              />
              <CreateButton
                label={
                  state.department?.length > 0
                    ? "selected_department"
                    : "select_department"
                }
                onAction={() => setStep(1)}
                customStyle={{
                  width: "fit-content",
                }}
                count={state.department?.length}
              />

              <CreateButton label="export" onAction={onFilter} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AdmissionFeeRegister;
