import { useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { useAdmissionPendingFees } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import AdmissionPendingOutstandingCard from "./AdmissionPendingOutstandingCard";
import { useAdmissionPendingApplicabelFeesExport } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const AdmissionPendingOutstanding = ({ aid, instituteId, isCashierAccess }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [disabled, setDisabled] = useState(false);

  const [admissionPendingApplicabelFeesExport] =
    useAdmissionPendingApplicabelFeesExport();
  const { admissionPendingFees, admissionPendingFeesLoading } =
    useAdmissionPendingFees({
      data: {
        aid: aid,
        page: page,
        limit: 10,
        search: search,
        flow: "All_Pending_Fees_Query",
      },
      skip: !aid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionPendingFees?.list?.length) {
      let obj = nextPage(admissionPendingFees?.list?.length);
      setState(() => obj);
    }
  }, [admissionPendingFees?.list]);

  const onSearchEvent = (e) => {
    if (
      !admissionPendingFeesLoading &&
      admissionPendingFees?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onExportList = () => {
    if (aid) {
      setDisabled((pre) => !pre);
      admissionPendingApplicabelFeesExport({
        aid: aid,
        flow: "APPLICABLE_OS",
      })
        .then((res) => {
          let url =
            getQuery.state?.urlOffSet === "Institute"
              ? `/q/${params.username}/institute/member/finance?type=export`
              : `/q/${params.username}/member/staff/finance?type=export`;
          navigate(url, {
            state: getQuery.state,
          });
        })
        .catch({});
    }
  };
  // console.info("djkshfjkd", admissionPendingFees, search);
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.7rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
              name="search"
            />
          </div>
        </section>
        <CreateButton
          label="export"
          customStyle={{
            margin: "0",
            padding: "0.4rem 1rem",
            width: "fit-content",
          }}
          onAction={onExportList}
        />
      </div>

      {admissionPendingFees?.list?.map((student, index) =>
        admissionPendingFees?.list?.length === index + 1 ? (
          <div key={student?._id} ref={ref}>
            <AdmissionPendingOutstandingCard
              student={student}
              instituteId={instituteId}
              isCashierAccess={isCashierAccess}
            />
          </div>
        ) : (
          <AdmissionPendingOutstandingCard
            student={student}
            key={student?._id}
            instituteId={instituteId}
            isCashierAccess={isCashierAccess}
          />
        )
      )}

      {admissionPendingFeesLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        admissionPendingFees?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No student found related this search."
          />
        )
      ) : (
        admissionPendingFees?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No student found."
          />
        )
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionPendingOutstanding;
