import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import AdmissionPendingFeesWrapper from "./AdmissionPendingFeesWrapper";
import FinanceAllStudent from "../../FinanceManager/FinanceAllStudent/FinanceAllStudent";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import AdmissionPendingOutstanding from "./AdmissionPendingOutstanding";
import AdmissionPendingApplicableFees from "./AdmissionPendingApplicableFees";

const AdmissionPendingFeesConfig = ({
  instituteId,
  studentAlias,
  aid,
  fid,
  cashAuthority,
}) => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <UserMemberContainerWrapper>
      <AdmissionPendingFeesWrapper
        aid={aid}
        fid={fid}
        instituteId={instituteId}
      />
      {!searchParams.get("n") ? (
        <FinanceAllStudent
          instituteId={instituteId}
          studentAlias={studentAlias}
          invalidateAccess="unAuthorized"
          allStudentControl={allStudentControl}
          whichFlow="ADMISSION"
          otherData={{
            aid: aid,
            isCashierAccess: cashAuthority?.includes(getQuery?.state?.sid)
              ? "ALLOW"
              : "DENY",
            collectStaffId: getQuery?.state?.sid,
          }}
        />
      ) : searchParams.get("n") === "outstanding" ? (
        <UserMemberContentWrapper>
          <AdmissionPendingOutstanding
            aid={aid}
            instituteId={instituteId}
            isCashierAccess={
              cashAuthority?.includes(getQuery?.state?.sid) ? "ALLOW" : "DENY"
            }
          />
        </UserMemberContentWrapper>
      ) : searchParams.get("n") === "applicable" ? (
        <UserMemberContentWrapper>
          <AdmissionPendingApplicableFees
            aid={aid}
            instituteId={instituteId}
            isCashierAccess={
              cashAuthority?.includes(getQuery?.state?.sid) ? "ALLOW" : "DENY"
            }
          />
        </UserMemberContentWrapper>
      ) : null}
    </UserMemberContainerWrapper>
  );
};

export default AdmissionPendingFeesConfig;

const allStudentControl = {
  collectAccess: "ADMISSION_ADMIN",
  govtAccess: "Other",
  cancelAccess: false,
  editStructureControl: "authorized",
  editGovtNumberContorl: "authorized",
  editInstallmentControl: "authorized",
  editGovtInstallmentControl: "authorized",
  isSearchEnable: "authorized",
};
